const auth = new Auth();

var time = 0;

const handleAuthentication = (nextState, replace) => {
  if (/access_token|id_token|error/.test(nextState.location.hash)) {
    alert("handleAuth from call backrender");

    auth.handleAuthentication();
  }
}


class Callback extends Component
{
  constructor(props)
  {
    super(props)
    console.log("#### CALLBACK call back instantiated",props);

    store.dispatch({type:"SET_AUTH", auth: auth, history:props.history});
  }

  render()
  {
    //handles when user isn't authenticated and reland on the callback route, if not auth then goto login
    if(!store.getState().auth.isAuthenticated()) return(<Redirect to={store.getState().home_path+"/login"} />);

    return(
    <div className= "page-section-content">
        <div>Authenticating...</div>
    </div>)
  }
}


