
import auth0 from 'auth0-js';
import { connect } from 'react-redux';
import {withRouter} from 'react-router-dom';
import {Component} from 'react';
import store from './MyStore';

import {getAllAudio, getAllVideo, getAllArt, decodeJWT} from "./functions.js";

class Auth  extends Component{

    auth0 = new auth0.WebAuth({
    domain: 'future-fest.auth0.com',
    clientID: '1t9PSy6uXWIK_55xBfEV7auoNBU20PQ5',
    redirectUri: 'http://www.futurefestvr.com/account',
    audience: 'https://future-fest.auth0.com/userinfo',
    responseType: 'token id_token',
    scope: 'openid'
  });

  login() {
    this.auth0.authorize();
  }

  constructor(props) {
  	super(props)
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.handleAuthentication = this.handleAuthentication.bind(this);
    this.isAuthenticated = this.isAuthenticated.bind(this);
  }

  handleAuthentication() {
  	//alert("handleAuth from AUTH.js");

  	//console.log("handleAuth:", this.props, store.getState());

    this.auth0.parseHash((err, authResult) => {
    	console.log("AuthRes:", authResult,err);
    	//alert("pars came back");

    	if(authResult == null)
    	{
    		var state = store.getState();
    		var history = state.history;

    		history.replace(state.home_path+"/login");
    	}
    	

      if (authResult && authResult.accessToken && authResult.idToken) {

      	//alert("set seeging and aupdate sotre and acces granted");
        this.setSession(authResult);
        this.updateStoreLoginInfo(authResult);
        this.onAccessGranted(authResult);
        
        store.getState().history.push(store.getState().home_path+'/assets/0');
        //alert("idUpdated");

      } else if (err) {
        store.getState().history.push(store.getState().home_path+'/home');
        store.dispatch({type:"LOGOUT"});
        console.log(err);
      }
    });
  }

  handleAuthenticationFromSession()
  {
  	var token = localStorage.getItem("id_token");

  	if(token == null)
  	{
  		//alert("no toekn");
  		store.getState().history.replace(store.getState().home_path+'/home');
  		return;
  	}

  	this.updateStoreLoginInfo({idToken:token});
  	this.onAccessGranted();
  }

  updateStoreLoginInfo(authResult)
  {
  	var idToken = authResult.idToken;
  	//var acc = localStorage.getItem("access_token");

  	var decoded = decodeJWT(idToken);
  	//var accessTokenDec = decodeJWT(acc)

  	var artistID = parseInt(decoded["https://us-central1-lustrous-spirit-170901.cloudfunctions.net/artistId"]);
	//alert("ID:"+ artistID);
  	store.dispatch({type:"UPDATE_ARTIST_ID", artistID: artistID})
  	

  	console.log("### UPDATE STORE WITH LOGIN INFO ###", decoded, artistID, authResult);

  }

  onAccessGranted(authResult)
  {

  	var loginInfo = authResult;

  	store.dispatch({type:"LOGIN_COMPLETE", logged_in_user: "Online", login_info: authResult});

  	console.log("ACCESS GRANTED PROPS:", this.props);

	//dispatches to hide to loading screen
	store.dispatch({type:"WAIT_SCREEN", show_wait_screen: false});
	store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: true});
	store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: true});
	store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: true});

	getAllAudio(function(tracks){
		store.dispatch({type:"REFRESH_MUSIC_TRACKS", tracks:tracks});
		store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});
	});

	getAllVideo(function(tracks_video){
		store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: false});
		store.dispatch({type:"REFRESH_VIDEO_TRACKS", tracks_video: tracks_video});
	});

	getAllArt(function(art_list){
		store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});
		store.dispatch({type:"REFRESH_ART_LIST", art_list: art_list})
	});
  }

  setSession(authResult) {
    // Set the time that the access token will expire at
    let expiresAt = JSON.stringify((authResult.expiresIn * 1000) + new Date().getTime());
    localStorage.setItem('access_token', authResult.accessToken);
    localStorage.setItem('id_token', authResult.idToken);
    localStorage.setItem('expires_at', expiresAt);

    // navigate to the home route
	//this.onAccessGranted();
  }


  logout() {
    // Clear access token and ID token from local storage
    localStorage.removeItem('access_token');
    localStorage.removeItem('id_token');
    localStorage.removeItem('expires_at');

    store.dispatch({type:"LOGOUT"});
    store.getState().history.replace(store.getState().home_path+'/home');
  }

  isAuthenticated() {
    // Check whether the current time is past the 
    // access token's expiry time
    let expiresAt = JSON.parse(localStorage.getItem('expires_at'));
    return new Date().getTime() < expiresAt;
  }
}
